let currentMusic = null;

export const audioManager = {
  playMusic(k, name, options = {}) {
    // Stop existing music
    if (currentMusic && currentMusic.stop) {
      currentMusic.stop();
      currentMusic = null;
    }

    try {
      currentMusic = k.play(name, {
        loop: true,
        volume: options.volume ?? 0.2, // Use provided volume or default to 0.5
      });
    } catch (err) {
      console.error(`❌ Failed to play music: "${name}"`, err);
    }

    return currentMusic;
  },

  stopMusic() {
    if (currentMusic && currentMusic.stop) {
      currentMusic.stop();
      currentMusic = null;
    }
  },
};

