import { playerState, yukiState, gameState } from "../state/stateManagers.js";
import { dialog } from "../uiComponents/dialog.js";
import { playAnimIfNotPlaying } from "../utils.js";
import yukiLines, { yukiVoiceLines } from "../content/yukiDialogue.js";
import { healthBar } from "../uiComponents/healthbar.js";

export function generateYukiComponents(k, pos) {
  return [
    k.sprite("assets", { anim: "yuki-down" }),
    k.area({ shape: new k.Rect(k.vec2(2, 4), 30, 12) }),
    k.body({ isStatic: true }),
    k.pos(pos),
    "yuki",
  ];
}

export async function startInteraction(k, yuki, player) {
  // 🧍 Face player direction
  const direction = player.direction;
  if (direction === "left") {
    yuki.flipX = false;
    playAnimIfNotPlaying(yuki, "yuki-side");
  } else if (direction === "right") {
    yuki.flipX = true;
    playAnimIfNotPlaying(yuki, "yuki-side");
  } else if (direction === "down") {
    playAnimIfNotPlaying(yuki, "yuki-up");
  }

  const responses = yukiLines;
  const voices = yukiVoiceLines || [];

  if (!Array.isArray(responses) || responses.length === 0) {
    console.warn("⚠️ No dialogue lines defined for Yuki.");
    return;
  }

  let nbTalkedYuki = yukiState.getNbTalkedYuki();

  // 🎁 First-time heart gift
  if (nbTalkedYuki === 1 && !yukiState.getHasGivenHeart()) {
    await dialog(
      k,
      k.vec2(0, 0),
      [responses[1]],
      false,
      "yukiPortrait",
      voices[1] ? [voices[1]] : [],
      "Yuki"
    );

    if (playerState.getCurrentMaxHealth() < playerState.getTrueMaxHealth()) {
      playerState.setCurrentMaxHealth(playerState.getCurrentMaxHealth() + 1);
      playerState.setHealth(playerState.getCurrentMaxHealth());
      k.destroyAll("healthContainer");
      healthBar(k);
    }

    yukiState.setHasGivenHeart(true);
    yukiState.setNbTalkedYuki(nbTalkedYuki + 1);
    return;
  }

  // 🔁 Loop dialogue
  if (nbTalkedYuki >= responses.length) {
    nbTalkedYuki = 0;
    yukiState.setNbTalkedYuki(0);
  }

  if (responses[nbTalkedYuki]) {
    await dialog(
      k,
      k.vec2(0, 0),
      [responses[nbTalkedYuki]],
      false,
      "yukiPortrait",
      voices[nbTalkedYuki] ? [voices[nbTalkedYuki]] : [],
      "Yuki"
    );
    yukiState.setNbTalkedYuki(nbTalkedYuki + 1);
  }
}

export function endInteraction(yuki) {
  playAnimIfNotPlaying(yuki, "yuki-down");
}



