import { gameState } from "../state/stateManagers.js";
import { colorizeBackground } from "../utils.js";
import { audioManager } from "../state/audioManager.js";

export default function endingCredits(k) {
    // 🎵 Play background music
  audioManager.playMusic(k, "endingMusic"); 

  // 🎨 Background
  colorizeBackground(k, 0, 0, 0);

  // 🌅 Background image
  k.add([
    k.sprite("endingCredits"),
    k.anchor("center"),
    k.scale(k.width() / 600, k.height() / 400),
    k.pos(k.center()),
  ]);

  k.add([
    k.text("Thanks for Playing!", { size: 48 }),
    k.pos(k.center().x, 150),
    k.anchor("center"),
    k.color(0, 0, 0),
  ]);

  k.add([
    k.text("Created by Shanel L0g1cF@11acy Locke\nWith Support From Mint Pitassy\n\nPress ENTER to Restart", {
      size: 24,
      width: 500,
      lineSpacing: 20,
    }),
    k.pos(k.center().x, 300),
    k.anchor("top"),
    k.color(0, 0, 0),
  ]);

  k.onKeyPress("enter", () => {
    k.go("mainMenu"); // Or your starting scene
  });
}
