export default function naokoGlobalStateManager() {
  let instance = null;

  function createInstance() {
    // Private state
    let nbTalkedNaoko = 0;
    let hasGivenSpecialItem = false;

    return {
      // Number of times Naoko has been talked to
      setNbTalkedNaoko(value) {
        nbTalkedNaoko = value;
      },
      getNbTalkedNaoko() {
        return nbTalkedNaoko;
      },

      // Has Naoko given their high-value item?
      setHasGivenSpecialItem(value) {
        hasGivenSpecialItem = value;
      },
      getHasGivenSpecialItem() {
        return hasGivenSpecialItem;
      },
    };
  }

  return {
    getInstance() {
      if (!instance) {
        instance = createInstance();
      }
      return instance;
    },
  };
}
