export default function kojiGlobalStateManager() {
  let instance = null;

  function createInstance() {
    // Private state variables
    let nbTalkedKoji = 0;
    let hasGivenHeart = false;

    return {
      // Number of times Koji has been talked to
      setNbTalkedKoji(value) {
        nbTalkedKoji = value;
      },
      getNbTalkedKoji() {
        return nbTalkedKoji;
      },

      // Has the heart reward been given?
      setHasGivenHeart(value) {
        hasGivenHeart = value;
      },
      getHasGivenHeart() {
        return hasGivenHeart;
      },
    };
  }

  return {
    getInstance() {
      if (!instance) {
        instance = createInstance();
      }
      return instance;
    },
  };
}