export default function oldManGlobalStateManager() {
  let instance = null;

  function createInstance() {
    let nbTalkedOldMan = 0;
    let hasGivenHeart = false;

    return {
      setNbTalkedOldMan(value) {
        nbTalkedOldMan = value;
      },
      getNbTalkedOldMan: () => nbTalkedOldMan,

      getHasGivenHeart: () => hasGivenHeart,
      setHasGivenHeart: (value) => {
        hasGivenHeart = value;
      },
    };
  }

  return {
    getInstance() {
      if (!instance) {
        instance = createInstance();
      }
      return instance;
    },
  };
}

