import menuText from "../content/menuText.js";
import { gameState } from "../state/stateManagers.js";
import { colorizeBackground } from "../utils.js";
import { audioManager } from "../state/audioManager.js";

export default function mainMenu(k) {
  // 🎵 Play background music
  audioManager.playMusic(k, "menuMusic");

  // 🎨 Background
  colorizeBackground(k, 0, 0, 0);

  // 🌅 Background image
  k.add([
    k.sprite("sunset"),
    k.anchor("center"),
    k.scale(k.width() / 700, k.height() / 500),
    k.pos(k.center()),
  ]);

  // 🪧 Text background
  k.add([
    k.rect(k.width() * 0.8, 170, { radius: 16 }),
    k.pos(k.width() * 0.1, k.center().y - 140),
    k.color(0, 0, 0),
    k.opacity(0.75),
  ]);

  // 🕹 Title
  k.add([
    k.text(menuText.title, { size: 80, font: "gameboy" }),
    k.area(),
    k.anchor("center"),
    k.pos(k.center().x, k.center().y - 75),
  ]);

  // 🎮 Prompt
  k.add([
    k.text(menuText.playIndication, { size: 24, font: "gameboy" }),
    k.area(),
    k.anchor("center"),
    k.pos(k.center().x, k.center().y - 5),
  ]);

  // ⌨️ Start game
  k.onKeyPress("enter", () => {
    gameState.setFontSize(25); // or whatever default you're using
    audioManager.stopMusic();
    k.go("world");
  });
}



