export default function yukiGlobalStateManager() {
  let instance = null;

  function createInstance() {
    // Private state variables
    let nbTalkedYuki = 0;
    let hasGivenHeart = false;

    return {
      // Number of times Yuki has been talked to
      setNbTalkedYuki(value) {
        nbTalkedYuki = value;
      },
      getNbTalkedYuki() {
        return nbTalkedYuki;
      },

      // Has the heart reward been given?
      setHasGivenHeart(value) {
        hasGivenHeart = value;
      },
      getHasGivenHeart() {
        return hasGivenHeart;
      },
    };
  }

  return {
    getInstance() {
      if (!instance) {
        instance = createInstance();
      }
      return instance;
    },
  };
}


