import { playerState, oldManState, gameState } from "../state/stateManagers.js";
import { dialog } from "../uiComponents/dialog.js";
import { playAnimIfNotPlaying } from "../utils.js";
import { oldmanLines, voiceLinesMap } from "../content/oldmanDialogue.js";
import { healthBar } from "../uiComponents/healthbar.js";

export function generateOldManComponents(k, pos) {
  console.log("Generating Old Man at position", pos);
  return [
    k.sprite("assets", { anim: "oldman-down" }),
    k.area({ shape: new k.Rect(k.vec2(0), 20, 12) }),
    k.body({ isStatic: true }),
    k.pos(pos),
    "oldman",
  ];
}

export async function startInteraction(k, oldman, player) {
  // Face the player (left and right flipped)
  if (player.direction === "left") {
    oldman.flipX = false;
    playAnimIfNotPlaying(oldman, "oldman-side");
  } else if (player.direction === "right") {
    oldman.flipX = true;
    playAnimIfNotPlaying(oldman, "oldman-side");
  } else if (player.direction === "down") {
    playAnimIfNotPlaying(oldman, "oldman-up");
  }

  // ✅ These must be declared in this order
  const scene = gameState.getCurrentScene();
  const responses = oldmanLines[scene] || oldmanLines.world || [];
  const voiceLines = voiceLinesMap[scene] || voiceLinesMap.world || [];


  console.log("📍 Scene:", scene);
  console.log("📚 Dialogue lines:", responses);
  console.log("🔊 Voice lines:", voiceLines);

  playerState.setIsSwordEquipped(true);

  // If the son is already saved, use fixed line
  if (gameState.getIsSonSaved()) {
    await dialog(
      k,
      k.vec2(0, 0),
      [responses[3]],
      false,
      "oldmanPortrait",
      voiceLines[3] ? [voiceLines[3]] : [],
      "Tetsuya"
    );
    return;
  }

  let nbTalkedOldMan = oldManState.getNbTalkedOldMan();

  // Give heart after first conversation
  if (nbTalkedOldMan === 1 && !oldManState.getHasGivenHeart()) {
    await dialog(
      k,
      k.vec2(0, 0),
      [responses[nbTalkedOldMan]],
      false,
      "oldmanPortrait",
      voiceLines[nbTalkedOldMan] ? [voiceLines[nbTalkedOldMan]] : [],
      "Tetsuya"
    );

    if (playerState.getCurrentMaxHealth() < playerState.getTrueMaxHealth()) {
      playerState.setCurrentMaxHealth(playerState.getCurrentMaxHealth() + 1);
      playerState.setHealth(playerState.getCurrentMaxHealth());
      k.destroyAll("healthContainer");
      healthBar(k);
    }

    oldManState.setHasGivenHeart(true);
    oldManState.setNbTalkedOldMan(nbTalkedOldMan + 1);
    return;
  }

  // Loop back to first line if we're out of lines
  if (nbTalkedOldMan >= responses.length) {
    oldManState.setNbTalkedOldMan(0);
    nbTalkedOldMan = 0;
  }

  const line = responses[nbTalkedOldMan];
  const voice = voiceLines[nbTalkedOldMan];

  await dialog(
    k,
    k.vec2(0, 0),
    [line],
    false,
    "oldmanPortrait",
    voice ? [voice] : [],
    "Tetsuya"
  );

  oldManState.setNbTalkedOldMan(nbTalkedOldMan + 1);
}

export function endInteraction(oldman) {
  playAnimIfNotPlaying(oldman, "oldman-down");
}
