import { gameState } from "../state/stateManagers.js";

async function displayLine(textContainer, line) {
  for (const char of line) {
    await new Promise((resolve) =>
      setTimeout(() => {
        textContainer.text += char;
        resolve();
      }, 10)
    );
  }
}

export async function dialog(
  k,
  pos,
  content,
  instant = false,
  portraitName = null,
  voices = [],
  npcName = null,
  headerText = null // ✅ New optional header
) {
  gameState.setFreezePlayer(true);
  k.play("soundUI");

  const centerX = k.width() / 2;
  const dialogY = k.height() - 220;
  const portraitX = centerX - 380;
  const portraitY = dialogY;

  // 🎴 Portrait + Frame + Label
  let portraitFrame, portraitImage, nameLabel;

  if (portraitName) {
    portraitFrame = k.add([
      k.rect(120, 150, { radius: 12 }),
      k.color(255, 255, 255),
      k.pos(portraitX, portraitY),
      k.z(1001),
      k.fixed(),
      k.opacity(0.85),
    ]);

    // ✅ Add sprite frame mappings for different portrait names
  const portraitFrames = {
    oldmanPortrait: 1159,
    yukiPortrait: 1034,
    satoshiPortrait: 1018,
    kojiPortrait: 1026,
    mariPortrait: 1030,
    naokoPortrait: 1022,
  // add others as needed...
    };

  const portraitFrameIndex = portraitFrames[portraitName] || 1163; // fallback to oldman

    portraitImage = k.add([
    k.sprite("assets", { frame: portraitFrameIndex }),
      k.pos(portraitX + 25, portraitY + 10),
      k.scale(5.2),
      k.z(1002),
      k.fixed(),
    ]);

    if (npcName) {
      nameLabel = k.add([
        k.text(npcName, {
          font: "gameboy",
          size: 22,
        }),
        k.color(0, 0, 0),
        k.pos(portraitX + 60, portraitY + 125),
        k.anchor("center"),
        k.z(1003),
        k.fixed(),
      ]);
    }
  }

  // 🪪 Item Header Box (Optional)
  let headerBox, headerLabel;
  if (headerText) {
    headerBox = k.add([
      k.rect(250, 36, { radius: 6 }),
      k.color(255, 255, 255),
      k.pos(centerX - 250, dialogY - 40),
      k.z(999),
      k.fixed(),
      k.opacity(0.85),
    ]);

    headerLabel = k.add([
      k.text(headerText, {
        font: "gameboy",
        size: 30,
      }),
      k.color(0, 0, 0),
      k.pos(centerX - 135, dialogY - 22),
      k.anchor("center"),
      k.z(1000),
      k.fixed(),
    ]);
  }

  // 💬 Dialogue Box
  const dialogBox = k.add([
    k.rect(680, 180, { radius: 12 }),
    k.color(255, 255, 255),
    k.pos(centerX - 250, dialogY),
    k.z(1000),
    k.fixed(),
    k.opacity(0.85),
  ]);

  const textContainer = dialogBox.add([
    k.text("", {
      font: "gameboy",
      width: 640,
      lineSpacing: 12,
      size: gameState.getFontSize(),
    }),
    k.color(0, 0, 0),
    k.pos(20, 30),
    k.fixed(),
  ]);

  const continuePrompt = dialogBox.add([
    k.text("Press SPACE", {
      font: "gameboy",
      size: gameState.getFontSize() - 4,
    }),
    k.color(0, 0, 0),
    k.pos(270, 140),
    k.fixed(),
    k.opacity(0),
  ]);

  let blinkTimer = 0;
  const blinkUpdate = k.onUpdate(() => {
    if (continuePrompt.opacity > 0) {
      blinkTimer += k.dt();
      continuePrompt.hidden = Math.floor(blinkTimer * 2) % 2 === 0;
    }
  });

  let index = 0;
  let lineFinishedDisplayed = false;

  if (!Array.isArray(content)) {
    console.warn("⚠️ 'content' passed to dialog() is not an array:", content);
    return;
  }

  const playVoice = (idx) => {
    if (voices && voices[idx]) {
      try {
        k.play(voices[idx]);
      } catch (e) {
        console.warn(`⚠️ Couldn't play voice line: ${voices[idx]}`, e);
      }
    }
  };

  playVoice(index);

  if (instant) {
    textContainer.text = content[index];
  } else {
    await displayLine(textContainer, content[index]);
  }

  continuePrompt.opacity = 1;
  lineFinishedDisplayed = true;

  const dialogKey = k.onKeyPress("space", async () => {
    if (!lineFinishedDisplayed) return;

    index++;

    if (!content[index]) {
      k.destroy(dialogBox);
      k.destroyAll("interactPrompt");
      if (portraitImage) k.destroy(portraitImage);
      if (portraitFrame) k.destroy(portraitFrame);
      if (nameLabel) k.destroy(nameLabel);
      if (headerBox) k.destroy(headerBox);
      if (headerLabel) k.destroy(headerLabel);
      dialogKey.cancel();
      blinkUpdate.cancel();
      gameState.setFreezePlayer(false);
      return;
    }

    playVoice(index);
    textContainer.text = "";
    continuePrompt.opacity = 0;
    lineFinishedDisplayed = false;

    if (instant) {
      textContainer.text = content[index];
    } else {
      await displayLine(textContainer, content[index]);
    }

    continuePrompt.opacity = 1;
    lineFinishedDisplayed = true;
  });
}
