import { playerState, naokoState, gameState } from "../state/stateManagers.js";
import { dialog } from "../uiComponents/dialog.js";
import { playAnimIfNotPlaying } from "../utils.js";
import { naokoLines, naokoVoiceLines } from "../content/naokoDialogue.js";
import { healthBar } from "../uiComponents/healthbar.js";

export function generateNaokoComponents(k, pos) {
  return [
    k.sprite("assets", { anim: "naoko-down" }),
    k.area({ shape: new k.Rect(k.vec2(2, 4), 35, 35) }),
    k.body({ isStatic: true }),
    k.pos(pos),
    "naoko",
  ];
}

export async function startInteraction(k, naoko, player) {
  // 🔁 Face the player (with flipped L/R logic)
  if (player.direction === "left") {
    naoko.flipX = false;
    playAnimIfNotPlaying(naoko, "naoko-side");
  } else if (player.direction === "right") {
    naoko.flipX = true;
    playAnimIfNotPlaying(naoko, "naoko-side");
  } else if (player.direction === "down") {
    playAnimIfNotPlaying(naoko, "naoko-up");
  }

  const responses = naokoLines;
  const voiceLines = naokoVoiceLines;

  if (!Array.isArray(responses)) {
    console.warn("⚠️ Naoko dialogue is not a valid array:", responses);
    return;
  }

  playerState.setIsSwordEquipped(true);

  let nbTalkedNaoko = naokoState.getNbTalkedNaoko();

  // 🔁 Reset loop if we reach the end
  if (nbTalkedNaoko >= responses.length) {
    naokoState.setNbTalkedNaoko(0);
    nbTalkedNaoko = 0;
  }

  const line = responses[nbTalkedNaoko];
  const voice = voiceLines[nbTalkedNaoko];

  await dialog(
    k,
    k.vec2(0, 0),
    [line],
    false,
    "naokoPortrait",
    voice ? [voice] : [],
    "Naoko"
  );

  naokoState.setNbTalkedNaoko(nbTalkedNaoko + 1);
}

export function endInteraction(naoko) {
  playAnimIfNotPlaying(naoko, "naoko-down");
}
