import { playerState, mariState, gameState } from "../state/stateManagers.js";
import { dialog } from "../uiComponents/dialog.js";
import { playAnimIfNotPlaying } from "../utils.js";
import { mariLines, mariVoiceLines } from "../content/mariDialogue.js";
import { healthBar } from "../uiComponents/healthbar.js";

// 🎀 Generate Mari in the scene
export function generateMariComponents(k, pos) {
  return [
    k.sprite("assets", { anim: "mari-down" }),
    k.area({ shape: new k.Rect(k.vec2(2, 4), 18, 12) }),
    k.body({ isStatic: true }),
    k.pos(pos),
    "mari",
  ];
}

// 💬 Mari dialogue interaction
export async function startInteraction(k, mari, player) {
  const originalDirection = player.direction;
  player.direction = "down";

  const facingMap = {
    left: () => {
      mari.flipX = false;
      playAnimIfNotPlaying(mari, "mari-side");
    },
    right: () => {
      mari.flipX = true;
      playAnimIfNotPlaying(mari, "mari-side");
    },
    down: () => playAnimIfNotPlaying(mari, "mari-up"),
  };

  if (facingMap[originalDirection]) facingMap[originalDirection]();

  const responses = mariLines;
  const voiceLines = mariVoiceLines;

  if (!Array.isArray(responses)) {
    console.warn("⚠️ Mari dialogue is not a valid array:", responses);
    return;
  }

  playerState.setIsSwordEquipped(true);

  let nbTalkedMari = mariState.getNbTalkedMari();

  if (nbTalkedMari >= responses.length) {
    mariState.setNbTalkedMari(0);
    nbTalkedMari = 0;
  }

  await dialog(
    k,
    k.vec2(0, 0),
    [responses[nbTalkedMari]],
    false,
    "mariPortrait",
    [voiceLines[nbTalkedMari]],
    "Mari"
  );

  mariState.setNbTalkedMari(nbTalkedMari + 1);
  player.direction = originalDirection;
}

export function endInteraction(mari) {
  playAnimIfNotPlaying(mari, "mari-down");
}
