export default function satoshiGlobalStateManager() {
  let instance = null;

  function createInstance() {
    let nbTalkedSatoshi = 0;
    let hasGivenHeart = false;

    return {
      setNbTalkedSatoshi(value) {
        nbTalkedSatoshi = value;
      },
      getNbTalkedSatoshi: () => nbTalkedSatoshi,

      getHasGivenHeart: () => hasGivenHeart,
      setHasGivenHeart: (value) => {
        hasGivenHeart = value;
      },
    };
  }

  return {
    getInstance() {
      if (!instance) {
        instance = createInstance();
      }
      return instance;
    },
  };
}

