export default function mariGlobalStateManager() {
  let instance = null;

  function createInstance() {
    // Private state variables
    let nbTalkedMari = 0;
    let hasGivenHeart = false;

    return {
      // Number of times Mari has been talked to
      setNbTalkedMari(value) {
        nbTalkedMari = value;
      },
      getNbTalkedMari() {
        return nbTalkedMari;
      },

      // Has the heart reward been given?
      setHasGivenHeart(value) {
        hasGivenHeart = value;
      },
      getHasGivenHeart() {
        return hasGivenHeart;
      },
    };
  }

  return {
    getInstance() {
      if (!instance) {
        instance = createInstance();
      }
      return instance;
    },
  };
}

