import { playerState, satoshiState, gameState } from "../state/stateManagers.js";
import { dialog } from "../uiComponents/dialog.js";
import { playAnimIfNotPlaying } from "../utils.js";
import { satoshiLines, satoshiVoiceLines } from "../content/satoshiDialogue.js";
import { healthBar } from "../uiComponents/healthbar.js";

export function generateSatoshiComponents(k, pos) {
  console.log("Generating Satoshi at position", pos);
  return [
    k.sprite("assets", { anim: "satoshi-down" }),
    k.area({ shape: new k.Rect(k.vec2(2, 4), 15, 10) }),
    k.body({ isStatic: true }),
    k.pos(pos),
    "satoshi",
  ];
}

export async function startInteraction(k, satoshi, player) {
  // Face the player (left and right flipped)
  if (player.direction === "left") {
    satoshi.flipX = false;
    playAnimIfNotPlaying(satoshi, "satoshi-side");
  } else if (player.direction === "right") {
    satoshi.flipX = true;
    playAnimIfNotPlaying(satoshi, "satoshi-side");
  } else if (player.direction === "down") {
    playAnimIfNotPlaying(satoshi, "satoshi-up");
  }

  const scene = gameState.getCurrentScene();
  const responses = satoshiLines[scene] || satoshiLines.default || satoshiLines;
  const voiceLines = satoshiVoiceLines[scene] || satoshiVoiceLines.default || satoshiVoiceLines;

  console.log("📍 Scene:", scene);
  console.log("📚 Dialogue lines:", responses);
  console.log("🔊 Voice lines:", voiceLines);

  playerState.setIsSwordEquipped(true);

  // Alternate dialogue if son is saved
  if (gameState.getIsSonSaved()) {
    await dialog(
      k,
      k.vec2(0, 0),
      [responses[3]],
      false,
      "satoshiPortrait",
      voiceLines[3] ? [voiceLines[3]] : [],
      "Satoshi"
    );
    return;
  }

  let nbTalkedSatoshi = satoshiState.getNbTalkedSatoshi();

  // Give heart after second line
  if (nbTalkedSatoshi === 1 && !satoshiState.getHasGivenHeart()) {
    await dialog(
      k,
      k.vec2(0, 0),
      [responses[1]],
      false,
      "satoshiPortrait",
      voiceLines[1] ? [voiceLines[1]] : [],
      "Satoshi"
    );

    if (playerState.getCurrentMaxHealth() < playerState.getTrueMaxHealth()) {
      playerState.setCurrentMaxHealth(playerState.getCurrentMaxHealth() + 1);
      playerState.setHealth(playerState.getCurrentMaxHealth());
      k.destroyAll("healthContainer");
      healthBar(k);
    }

    satoshiState.setHasGivenHeart(true);
    satoshiState.setNbTalkedSatoshi(nbTalkedSatoshi + 1);
    return;
  }

  // Loop back to first line
  if (nbTalkedSatoshi >= responses.length) {
    satoshiState.setNbTalkedSatoshi(0);
    nbTalkedSatoshi = 0;
  }

  const line = responses[nbTalkedSatoshi];
  const voice = voiceLines[nbTalkedSatoshi];

  await dialog(
    k,
    k.vec2(0, 0),
    [line],
    false,
    "satoshiPortrait",
    voice ? [voice] : [],
    "Satoshi"
  );

  satoshiState.setNbTalkedSatoshi(nbTalkedSatoshi + 1);
}

export function endInteraction(satoshi) {
  playAnimIfNotPlaying(satoshi, "satoshi-down");
}
