import {
  endInteraction,
  generateOldManComponents,
  startInteraction,
} from "../entities/oldman.js";

import {
  generateSignBathComponents,
  startSignInteraction,
} from "../entities/items/signBath.js";

import {
  generatePlayerComponents,
  setPlayerControls,
  watchPlayerHealth,
} from "../entities/player.js";

import { gameState } from "../state/stateManagers.js";
import { healthBar } from "../uiComponents/healthbar.js";
import {
  colorizeBackground,
  drawBoundaries,
  drawTiles,
  fetchMapData,
} from "../utils.js";

import { audioManager } from "../state/audioManager.js";
import { createInteractPrompt } from "../uiComponents/itemPrompt.js";

let nearbyItem = null;

export default async function bath(k) {
  gameState.setCurrentScene("bath");
  audioManager.playMusic(k, "bathMusic");
  colorizeBackground(k, 27, 29, 52);

  const mapData = await fetchMapData("./assets/maps/bath.json");
  const map = k.add([k.pos(480, 200)]);
  const interactPrompt = createInteractPrompt(k);

  const entities = {
    player: null,
    oldman: null,
  };

  // 🔁 Load Tiled layers
  for (const layer of mapData.layers) {
    if (layer.name === "Boundaries") {
      drawBoundaries(k, map, layer);
      continue;
    }

    if (layer.name === "SpawnPoints") {
      for (const obj of layer.objects) {
        const pos = k.vec2(obj.x, obj.y);

        if (obj.name === "player") {
          entities.player = map.add(generatePlayerComponents(k, pos));
          entities.player.z = 2; // Player in front
          k.camPos(pos);
        }

        if (obj.name === "oldman" || obj.name === "old-man") {
          const oldman = map.add(generateOldManComponents(k, pos));
          oldman.z = 1; // Behind player
          entities.oldman = oldman;
        }

        if (obj.name === "signbath") {
          const sign = map.add(generateSignBathComponents(k, pos));
          sign.itemName = "signbath";
          sign.z = 1;
        }
      }

      continue;
    }

    drawTiles(k, map, layer, mapData.tileheight, mapData.tilewidth);
  }

  // 🎮 Controls & camera
  k.camScale(4);
  setPlayerControls(k, entities.player);

 k.onUpdate(() => {
    const target = entities.player.worldPos();
    const current = k.camPos();
    k.camPos(current.lerp(target, 0.1));

    if (nearbyItem) {
    interactPrompt.setPosition(nearbyItem.worldPos());
  }

  });

  // 🚪 Exit to world
  entities.player.onCollide("door-exit", () => {
    gameState.setPreviousScene("bath");
    audioManager.stopMusic();
    k.go("world");
  });

  // 🧓 Old Man
  entities.player.onCollide("oldman", async () => {
    await startInteraction(k, entities.oldman, entities.player);
  });

  entities.player.onCollideEnd("oldman", () => {
    endInteraction(entities.oldman);
  });

  // 🪧 Signs (show/hide prompt)
  entities.player.onCollide("item", (obj) => {
    if (!obj?.itemName) return;
    nearbyItem = obj;
    interactPrompt.show();
  });

  entities.player.onCollideEnd("item", () => {
    nearbyItem = null;
    interactPrompt.hide();
  });

  // 🎮 Press E to interact
  k.onKeyPress("e", async () => {
    if (!nearbyItem) return;

    if (nearbyItem.itemName === "signbath") {
      await startSignInteraction(k); // from signBath.js
    }
  });

  // ❤️ UI
  healthBar(k);
  watchPlayerHealth(k);
}

