import { playerState, kojiState, gameState } from "../state/stateManagers.js";
import { dialog } from "../uiComponents/dialog.js";
import { playAnimIfNotPlaying } from "../utils.js";
import { kojiLines, kojiVoiceLines } from "../content/kojiDialogue.js";
import { healthBar } from "../uiComponents/healthbar.js";

export function generateKojiComponents(k, pos) {
  return [
    k.sprite("assets", { anim: "koji-down" }), 
    k.area({ shape: new k.Rect(k.vec2(2, 4), 18, 12) }),
    k.body({ isStatic: true }),
    k.pos(pos),
    "koji",
  ];
}

export async function startInteraction(k, koji, player) {
  const originalDirection = player.direction;
  player.direction = "down";

  const facingMap = {
    left: () => {
      koji.flipX = false;
      playAnimIfNotPlaying(koji, "koji-side");
    },
    right: () => {
      koji.flipX = true;
      playAnimIfNotPlaying(koji, "koji-side");
    },
    down: () => playAnimIfNotPlaying(koji, "koji-up"),
  };

  if (facingMap[originalDirection]) facingMap[originalDirection]();

  const responses = kojiLines;
  const voiceLines = kojiVoiceLines;

  if (!Array.isArray(responses)) {
    console.warn("⚠️ Koji dialogue is not a valid array:", responses);
    return;
  }

  playerState.setIsSwordEquipped(true);

  let nbTalkedKoji = kojiState.getNbTalkedKoji();

  // 💬 Loop dialogue if at end
  if (nbTalkedKoji >= responses.length) {
    kojiState.setNbTalkedKoji(0);
    nbTalkedKoji = 0;
  }

  // 🗨️ Play next line with portrait and optional voice
  await dialog(
    k,
    k.vec2(0, 0),
    [responses[nbTalkedKoji]],
    false,
    "kojiPortrait",
    [voiceLines[nbTalkedKoji]],
    "Koji"
  );

  kojiState.setNbTalkedKoji(nbTalkedKoji + 1);
  player.direction = originalDirection;
}

export function endInteraction(koji) {
  playAnimIfNotPlaying(koji, "koji-down");
}
